import { Component, OnInit } from '@angular/core';

import { DataService } from '../services/data.service'
import {DatePipe} from "@angular/common";


@Component({
  moduleId: module.id,
  selector: 'kk-today',
  templateUrl: 'today.component.html',
  styleUrls: ['today.component.css']
})
export class TodayComponent implements OnInit{

  dataReady: boolean = false;
  pictures: any = null;
  videos: any = null;
  frontEnd: any = null;
  andriod: any = null;
  ios: any = null;
  extend: any = null;

  constructor(private dataService: DataService) {}

  ngOnInit() {
    this.dataService.fetchByDate(new Date()).subscribe(
      data => {

        this.pictures = data.results['福利'];
        this.videos = data.results['休息视频'];
        this.frontEnd = data.results['前端'];
        
        this.dataReady = true;
      }
    );
  }

}
